<template>
    <div :class="className" :id="id" :style="{height:'300px',width:'500px',display:'inline-block'}" ref="myEchart">
    </div>
</template>
<script>
    export default {
        props: {
            className: {
                type: String,
                default: 'yourClassName'
            },
            id: {
                type: String,
                default: 'yourID'
            },
            width: {
                type: String,
                default: '500px'
            },
            height: {
                type: String,
                default: '500px'
            },
            chartType: {
                type: String,
                default: 1
            },
            chartData: {
                type: String,
                default: {}
            }
        },
        data() {
            return {
                chart: null,
                xList: [],
                yList: [],
            }
        },
        created() {
        },
        mounted() {
            this.initChart();
        },
        beforeDestroy() {
            if (!this.chart) {
                return
            }
            this.chart.dispose();
            this.chart = null;
        },
        watch: {
            chartData: 'initChartData'
        },
        methods: {
//      refreshChart(){
//        window.setInterval(this.initChart,6000)
//        console.log("")
//        console.log(new Date())
//      },
            initChart() {
                this.chart = this.$echarts.init(this.$refs.myEchart);
                // 
                let title = ""
                switch (this.chartType) {
                    case 1 :
                        title = "";
                        break;
                    case 2 :
                        title = "";
                        break;
                    case 3 :
                        title = "";
                        break;
                    case 4 :
                        title = "";
                        break;
                }
                this.chart.setOption({
                    title: {
                        text: title,
                        target: 'blank',//
                        textStyle: { //
                            Color: 'green',//
                            fontStyle: '',//
                        },
                        left: 'center'
                    },
                    color: ['#3398DB'],
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: { // 
                            type: 'shadow' // 'line' | 'shadow'
                        }
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: [{
                        type: 'category',
                        data: this.xList,
                        axisTick: {
                            alignWithLabel: true
                        }
                    }],
                    yAxis: [{
                        type: 'value',
                    }],
                    series: [{
                        name: '',
                        type: 'line',
                        barWidth: '60%',
                        data: this.yList
                    }]
                })
            },
            initChartData() {
                this.xList = [];
                this.yList = [];
                let cdata = this.chartData[this.chartType];
                console.log(cdata);
                for (let x in cdata) {
                    this.xList.push(x);
                    this.yList.push(cdata[x]);
                }
//        for(let i=0;i<cdata.length;i++){
//          console.log(cdata[i]);
////            for(let xkey in cdata[i]){
////              console.log(xkey);
////              this.xList.push(xkey);
////              this.yList.push(cdata[i][xkey]);
////            }
//        }
                this.initChart();
                console.log("");
            },
        }

    }
</script>
